import React, { useEffect } from "react";
import { Container, Button } from 'reactstrap';
//import {jumbotron} from 'bootstrap';
const Home =()=>{
    useEffect(()=>{
        document.title="Home || Learncodewith Dirgesh";
    },[]);

    return (
        <div className="text-center">
            
                <h1 >Learncodewith Durgesh</h1>
                <p>This is developed by Learncodewith Durgesh for learning purpose amd
                    backend is on spring boot and frontend on react js
                </p>
                <Container>
                    <Button color="primary" outline>Start using</Button>
                </Container>
            
        </div>
    );
}
export default Home;